	module.exports = {"pathContext":{"docHtml":"<h1>cli-boxes <a href=\"https://travis-ci.org/sindresorhus/cli-boxes\"><img src=\"https://travis-ci.org/sindresorhus/cli-boxes.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Boxes for use in the terminal</p>\n</blockquote>\n<p>The list of boxes is just a <a href=\"boxes.json\">JSON file</a> and can be used wherever.</p>\n<h2>Install</h2>\n<pre><code>$ npm install --save cli-boxes\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">const cliBoxes = require('cli-boxes');\n\nconsole.log(cliBoxes.single);\n/*\n{\n    \"topLeft\": \"┌\",\n    \"topRight\": \"┐\",\n    \"bottomRight\": \"┘\",\n    \"bottomLeft\": \"└\",\n    \"vertical\": \"│\",\n    \"horizontal\": \"─\"\n}\n*/\n</code></pre>\n<h2>API</h2>\n<h3>cliBoxes</h3>\n<h4><code>single</code></h4>\n<pre><code>┌────┐\n│    │\n└────┘\n</code></pre>\n<h4><code>double</code></h4>\n<pre><code>╔════╗\n║    ║\n╚════╝\n</code></pre>\n<h4><code>round</code></h4>\n<pre><code>╭────╮\n│    │\n╰────╯\n</code></pre>\n<h4><code>single-double</code></h4>\n<pre><code>╓────╖\n║    ║\n╙────╜\n</code></pre>\n<h4><code>double-single</code></h4>\n<pre><code>╒════╕\n│    │\n╘════╛\n</code></pre>\n<h4><code>classic</code></h4>\n<pre><code>+----+\n|    |\n+----+\n</code></pre>\n<h2>Related</h2>\n<ul>\n<li><a href=\"https://github.com/sindresorhus/boxen\">boxen</a> - Create boxes in the terminal</li>\n</ul>\n<h2>License</h2>\n<p>MIT © <a href=\"http://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/cli-boxes/readme","proj":"argo-ci"}}

